.FAQ {
    width: 100%;
    background-color: $color-default-ghost-white;
    padding: 120px 0 120px 0;

    @include vp-1199 {
        padding: 100px 0 100px 0; 
    }

    @include vp-767 {
        padding: 70px 0 70px 0; 
    }
}

.FAQ__title {
    font-family: "AlumniSans", Verdana, sans-serif;
    font-style: normal;
    font-size: 54px;
    line-height: 64.8px;
    color: $color-default-sapphire;
    letter-spacing: 0.03em;
    margin: 0;
    padding-left: 7.4%;
    padding-bottom: 39px;

    @include vp-1199 {
        font-size: 42px;
        line-height: 50.4px;
        padding-left: 0;
        padding-bottom: 40px;
        text-align: center;
    }

    @include vp-767 {
        ont-size: 32px;
        line-height: 38.4px;
        padding-bottom: 30px; 
    }
}

.FAQ__container {
    width: 84.92%;
    margin: 0 auto;

    @include vp-1199 {
        width: 88.28%;
    }

    @include vp-767 {
        width: 90.625%;
    }
}

.FAQ__tabs {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include vp-1199 {
       padding: 0;
    }

    @include vp-767 {
        
    }
}

.FAQ__controls {
    width: 22.41%;
    max-height: 246px;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 21.95%);
    align-content: space-between;

    @include vp-1199 {
       width: 100%;
       display: flex;
       flex-wrap: nowrap;
       justify-content: space-between;
       padding-bottom: 40px;
    }

    @include vp-767 {
        flex-wrap: wrap;
        padding-bottom: 30px;
    }
}

.FAQ__content {
    width: 74.14%;
    margin: 0;
    border-bottom: 1px solid rgba($color: $color-default-resolution-blue, $alpha: 0.2);
    
    @include vp-1199 {
        width: 100%;
     }
}

.FAQ__control {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 18px;
    line-height: 18px;
    color: rgba(16, 38, 84, 1);
    width: 100%;
    border: 1px solid rgba(16, 38, 84, 0.2);
    opacity: 1;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    

    &.is-active {
        border: none;
        text-decoration: none;
        background: $red-to-blue;
        color: $color-default-white;
    }

    @include vp-1199 {
        width: 23.89%;
        padding: 18px 0 18px 0;
    }

    @include vp-767 {
        width: 48.62%;
        padding: 14px 0 14px 0;
        font-size: 16px;
        line-height: 16px;
        height: 45.8333%;
    }
}

.FAQ__control:hover,
.FAQ__control:focus {
    opacity: 1;
    border: 1px solid rgba(16, 38, 84, 1);
    text-decoration: none;
    outline: none;

    &.is-active {
        opacity: 1;
        border: none;
    }
}

.FAQ__control:active {
    opacity: 1;
    border: none;
    text-decoration: none;
    background: $red-to-blue;
    color: $color-default-white;
    outline: none;
}

.FAQ__control:disabled {
    opacity: 0.2;
    border: 1px solid rgba(16, 38, 84, 0.2);
}

.FAQ__control:first-child,
.FAQ__control:not(:first-child) {
    padding: 0;

    @include vp-1199 {
        padding: 18px 0 18px 0;
    }
}
