.reviews {
    width: 100%;
    padding: 120px 0 120px 0;

    @include vp-1199 {
        padding: 91px 0 100px 0;
    }

    @include vp-767 {
        padding: 63px 0 70px 0;
    }
}

.reviews__container {
    width: 49.195%;
    min-width: 672px;
    margin: 0 auto;
    position: relative;

    @include vp-1199 {
        width: 88.28%;
        min-width: 678px;
    }

    @include vp-767 {
        width: 90.625%;
        min-width: 290px;
    }
}

.reviews__title {
    font-family: "AlumniSans", Verdana, sans-serif;
    font-style: normal;
    font-size: 54px;
    line-height: 64.8px;
    color: $color-default-sapphire;
    letter-spacing: 0.03em;
    text-align: center;
    margin: 0;
    margin-bottom: 37px;

    @include vp-1199 {
        font-size: 40px;
        line-height: 50.4px;
        margin-bottom: 40px;
    }

    @include vp-767 {
        font-size: 32px;
        line-height: 38.4px;
        margin-bottom: 42px;
    }
}