.super-games {
    width: 100%;
    padding: 120px 0 106px 0;

    @include vp-1199 {
        padding: 100px 0 100px 0;
    }

    @include vp-767 {
        padding: 70px 0 63px 0;
    }
}

.super-games__container {
    width: 84.92%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include vp-1199 {
        width: 88.28%;
    }

    @include vp-767 {
        width: 90.06%;
    }
}

.super-games__info {
    width: 43.1%;
    background-color: $color-default-torch-red;
    color: $color-default-white;
    position: relative;

    @include vp-767 {
        width: 100%;
        position: relative;
        height: 292px;
    }
}

.super-games__wrapper {
    width: 68.7%;
    max-width: 343px;
    height: 65.2%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    grid-template-columns: 73.18% 26.82%;
    grid-template-rows: 24.235% 24.235% 30.98% 20.56%;

    @include vp-1199 {
        width: 69.18%;
        height: 63.4%;
        max-width: 202px;
        grid-template-rows: 23.245% 23.245% 32.43% 21.08%;
        grid-template-columns: 72.28% 27.72%;
    }

    @include vp-767 {
        width: 69.65%;
        max-width: 202px;
        position: inherit;
    }
}

.super-games__title {
    font-family: "AlumniSans", Verdana, sans-serif;
    font-style: normal;
    font-size: 82px;
    line-height: 79px;
    letter-spacing: 0.03em;
    text-align: center;
    margin: 0;
    grid-column-start: 1;
    grid-row-start: 1;

    @include vp-1199 {
        font-size: 48px;
        line-height: 46.08px;
    }

    @include vp-767 {
        font-size: 48px;
        line-height: 46.08px;
    }
}

.super-games__list {
    margin: 0;
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 3;
}

.super-games__item {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    line-height: 27px;
    border-bottom: 1px solid $color-default-white;

    @include vp-1199 {
        font-size: 12px;
        line-height: 18px;
    }

    @include vp-767 {
        font-size: 12px;
        line-height: 18px;
    }
}

.super-games__item--grid1 {
    width: 72%;

    @include vp-1199 {
        width: 85%;
    }
}

.super-games__number {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 40px;
    line-height: 60px;
    margin: 0;
    position: relative;

    @include vp-1199 {
        font-size: 24px;
        line-height: 30px;
    }

    @include vp-767 {
        font-size: 24px;
        line-height: 36px;
    }
}

.super-games__date {
    display: grid;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    line-height: 21px;

    @include vp-1199 {
        font-size: 10px;
        line-height: 15px;
        top: 55%;
        left: 60%;
    }

    @include vp-767 {
        font-size: 10px;
        line-height: 15px;
    }
}  

.super-games__text {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    line-height: 27px;
    margin: 0;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
    padding-top: 30px;

    @include vp-1199 {
        font-size: 12px;
        line-height: 18px;
        padding-top: 15px;
    }

    @include vp-767 {
        font-size: 12px;
        line-height: 18px;
        padding-top: 20px;
    }
}

.super-games__link {
    border: 4px solid $color-default-white;
    /* text-transform: uppercase; */
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 4;
    position: relative;
    box-sizing: border-box;
}

.super-games__link a {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 12px;
    line-height: 14.06px;
    letter-spacing: 0.05em;
    color: $color-default-white;
    text-transform: uppercase;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    outline: none;

    @include vp-1199 {
        font-size: 10px;
        line-height: 11.72px;
    }

    @include vp-767 {
        font-size: 10px;
        line-height: 11.72px;
    }
}

.super-games__link:hover,
.super-games__link:focus-within {
    background-color: $color-default-white;
}

.super-games__link:hover a,
.super-games__link:focus-within a {
    color: $color-default-sapphire;
}

.super-games__photo {
    width: 56.9%;
    background-color: $color-default-sapphire;

    @include vp-767 {
        width: 100%;
    }
}

.super-games__media {
    width: 100%;
}