.promotions {
    width: 100%;
    padding: 135px 0 120px 0;

    @include vp-1199 {
        padding: 100px 0 100px 0;
    }

    @include vp-767 {
        padding: 63px 0 70px 0;
    }
}

.promotions__title {
    font-family: "AlumniSans", Verdana, sans-serif;
    font-style: normal;
    font-size: 54px;
    line-height: 64.8px;
    color: $color-default-sapphire;
    letter-spacing: 0.03em;
    padding-bottom: 34px;
    margin: 0;
    text-align: center;

    @include vp-1199 {
        font-size: 42px;
        line-height: 50.4px;
        padding-bottom: 40px;
    }

    @include vp-767 {
        font-size: 32px;
        line-height: 38.4px;
        padding-bottom: 42px;
    }
}

.promotions__container {
    width: 84.92%;
    min-height: 462px;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

    @include vp-1199 {
        width: 88.28%;
        min-height: 703px;
        flex-wrap: wrap;
        align-content: space-between;
    }

    @include vp-767 {
        width: 90.625%;
        min-height: 1080px;
    }
}

.promotions__wrapper-1 {
    width: 48.277%;
    display: flex;
    flex-wrap: nowrap;

    @include vp-1199 {
        width: 100%;
        flex-wrap: wrap;
        height: 65.72%;
        min-height: 462px;
        margin-bottom: 30px;
    }

    @include vp-767 {
        height: 55.37%;
        min-height: 598px;
    }
}

.promotions__wrapper-2 {
    width: 48.277%;
    min-height: 211px;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;

    @include vp-1199 {
        width: 100%;
        height: 30%;
        justify-content: space-between;
    }

    @include vp-767 {
        height: 41.85%;
        min-height: 452px;
    }
}

.promotions__unlimited {
    background-color: $color-default-torch-red;
    width: 50%;
    padding-left: 45px;
    position: relative;

    @include vp-1199 {
        width: 41.15%;
    }

    @include vp-767 {
        width: 100%;
        height: 50%;
        min-height: 299px;
        padding-left: 0;
        text-align: center;
    }
}

.promotions__border {
    width: 100%;
    background: $red-to-blue;;
    outline: none;
    height: 45.67%;
    min-height: 211px;
    position: relative;

    @include vp-1199 {
        width: 47.78%;
    }

    @include vp-767 {
        width: 100%;
        text-align: center;
    }
}

.promotions__border:not(:last-child) {
    justify-content: space-between;
}

.promotions__item {
    width: 97%;
    background: $color-default-white;
    height: 94%;
    cursor: pointer;
    margin: 0 auto;
    padding-left: 35px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50%, -50%);

    @include vp-767 {
        padding-left: 0;
    }
}

.promotions__heading {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 26px;
    line-height: 26px;
    margin: 0;
    color: $color-default-resolution-blue;
    padding-top: 35px;
}

.promotions__text {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 22.4px;
    margin: 0;
    color: $color-default-resolution-blue;
    padding-top: 20px;
    
    @include vp-767 {
        width: 79%;
        padding-left: 18%;
        padding-bottom: 20px;
    }
}

.promotions__link {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 12px;
    line-height: 14.06px;
    margin: 0;
    letter-spacing: 0.05em;
    text-decoration: underline;
    text-underline-offset: 4px;
    text-transform: uppercase;
    color: $color-default-resolution-blue;
    position: absolute;
    top: 80%;
    left: 0;
    transform:translate(-50%, -50%);
    margin-left: 70px;
    padding: 10px;
    outline: none;

    @include vp-767 {
        position: initial;
        padding: 0;
        margin: 0;
    }
}

.promotions__link:hover {
    opacity: 0.7;
}

.promotions__link:focus {
    border: 1px solid $color-default-sapphire;
    text-decoration: none;
    margin-left: 80px;
}

.promotions__link:active {
    opacity: 1;
    padding: 0;
    border: none;
    margin-left: 70px;
}

.promotions__link:disabled {
    opacity: 0.3;
}

.promotions__heading--white-color {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 28px;
    line-height: 43.68px;
    margin: 0;
    color: $color-default-white;
}

.promotions__year {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 70px;
    line-height: 109.2px;
    margin: 0;
    color: $color-default-white;
    margin-top: -20px;
}

.promotions__text--white-color {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    line-height: 25.2px;
    margin: 0;
    color: $color-default-white;
    width: 81%;
    padding: 0;

    @include vp-767 {
        width: 83%;
        padding-left: 17%;
        padding-bottom: 30px;
    }
}

.promotions__text--tablet-width {
    @include vp-1199 {
        width: 83%;
    }
}

.promotions__link--white-color {
    font-family: "RobotoCondensed", Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 14px;
    line-height: 16.41px;
    letter-spacing: 0.05em;
    margin: 0;
    color: $color-default-white;
    top: 90%;
    margin-left: 80px;
    padding: 10px;
    outline: none;

    @include vp-767 {
        position: initial;
        padding-left: 0;
        margin-left: 0;
    }
}

.promotions__link--white-color:hover {
    opacity: 0.8;
}

.promotions__link--white-color:focus {
    border: 1px solid $color-default-white;
    text-decoration: none;
    margin-left: 90px;
}

.promotions__link--white-color:active {
    opacity: 1;
    border: none;
    margin-left: 80px;
}

.promotions__link--white-color:disabled {
    opacity: 0.5;
}

.promotions__photo {
    width: 50%;

    @include vp-1199 {
        width: 58.85%;
    }

    @include vp-767 {
        width: 100%;
    }
}

.promotions__media {
    width: 100%;
    height: 100%;
}
